		//content
		#container{}
		#content{}
		.content-wrap{
			&__outer{}
			&__inner{}
			.withParents-progress{padding-top: 162px;
				&__area{position: relative;height: 606px;overflow: hidden;}
				&__box{position: relative;height: auto !important;
					.js-scroll{height: 616px;}
					.data-table{position: relative;padding-left: 320px;width: 660px;margin: 0 auto;padding-bottom: 30px;
						.fixed{
							&-area{overflow: hidden;border-radius: 10px 0 0 0;position: absolute;top: 0;left: 0;
								&:before{content:"";position: absolute;top: 0;left: 0;width: 100%;height: 50px;background-color: rgba(152, 198, 255, 0.5);}
							}
							&-box{
								.phonics{height: 194px;margin-top: 50px;background: url("/assets/images/with_parents_progress/bl_arrow_row01.gif") repeat-x 0 100%;}
								.read-aloud{height: 194px;margin-top: 30px;background: url("/assets/images/with_parents_progress/bl_arrow_row01.gif") repeat-x 0 100%;}
								.meet-greet{height: 194px;margin-top: 30px;background: url("/assets/images/with_parents_progress/bl_arrow_row01.gif") repeat-x 0 100%;}
							}
						}
						table{position: relative;width: 320px;height: 192px;}
						th{position: relative;height: 64px;@include font($notoecp, $font-r, 23px, 25px, #5d9eed);text-align: center;vertical-align: middle;background: #f7fcf8 url("/assets/images/with_parents_progress/bl_arrow_row01.gif") repeat-x 0 0;
							span{display: inline-block;
								&:after{content:"";position: absolute;top: 0;right: 0;height: 100%;width: 2px;background: url("/assets/images/with_parents_progress/bl_arrow_col01.gif") repeat-y 0 2px;}
							}
							&.th{
								&-tit01{font-size: 20px;font-weight: $font-b;}
								&-tit02{font-size: 18px;}
								&-tit03{font-size: 18px;}
							}
						}
						.swiper-slide{width: 132px !important;
							&:nth-last-child(1){
								.activity-list{
									&:after{display: none;}
									.btn{width: 132px;}
								}
							}
						}
					}
					.activity{width: 132px;
						//해당 주차
						&.this-week{
							.tit{width: 130px;color: #f4e850 !important;background-color: #5d9eed !important;
								&:before{content:"";position: absolute;top: 0;right: 0;width: 2px;height: 50px;background-color:rgba(152,198,255,0.5);}
							}
							.btn{
								&-activity{
									&.default{color: #f4e850;background-color: #5d9eed;}
									&.complete{color: #f4e850;background-color: #69c4fd;}
								}
							}
						}
						//해당 주차 전부완료
						&.all-complete{}
						.tit{width: 132px;height: 50px;@include font($notoecp, $font-r, 20px, 50px, #5d9eed);background-color: rgba(152, 198, 255, 0.5);}
						&-lists{position: relative;padding-bottom: 2px;background: url("/assets/images/with_parents_progress/bl_arrow_row01.gif") repeat-x 0 100%;
							&.phonics{}
							&.read-aloud{margin-top: 30px;}
							&.meet-greet{margin-top: 30px;}
						}
						&-list{height: 62px;margin-top: 2px;background: url("/assets/images/with_parents_progress/bl_arrow_row01.png") repeat-x 0 0;
							&:nth-child(1),
							&:nth-child(2){margin-top: 0;padding-top: 2px;
								.btn{height: 64px;}
							}
							&:after{content:"";position: absolute;top: 0;right: 0;height: 100%;width: 2px;background: url("/assets/images/with_parents_progress/bl_arrow_col01.gif") repeat-y 0 2px;}
						}
						.btn{width: 130px;position: relative;display: flex;justify-content: center;align-items: center;
							&:after{content:"";position: absolute;top: -2px;right: 0;height: 2px;width: 100%;background: url("/assets/images/with_parents_progress/bl_arrow_row01.png") repeat-x 0 0;}
							&-activity{position: relative;@include font($notoecp, $font-r, 21px, 50px, transparent);
								&:before{content:"";position: absolute;top: -2px;left: 0;width: 100%;height: 66px;border:4px solid #1e58eb;box-sizing: border-box;transform: scale(0.9);opacity: 0;transition: all 0.3s;z-index: 1;}
								&.default{color: #f16587;background-color: #fed7dc;letter-spacing: 2px;}
								&.complete{color: #fff;background-color: #69c4fd;}
								&.disabled{background-color: #eaeaea;}
								&.vacation{font-size: 19px;color: #cdcbcb;background-color: #f9f9f4;}
								&.review{
									&:before{transform: scale(1);opacity: 1;}
								}
							}
						}
					}
				}
				.swiper{
					&-container{position: relative;overflow: hidden;border-radius: 0 10px 0 0;}
					&-wrapper{}
					&-slide{

					}
					&-controller{position: absolute;top: 96px;left: 0;width: 100%;text-align: center;display: flex;justify-content: center;
						.month{width: 129px;@include font($notoecp, $font-b, 35px, 33px, #5d9eed);letter-spacing: 2px;}
						.btn{margin: 0 7px 0 8px;
							&-swiper{
								&-prev{
									&-month{width: 30px;height: 33px;background: url("/assets/images/with_parents_progress/icon_prev01_month.png") repeat-x 0 0;
										&.active{background-image: url("/assets/images/with_parents_progress/icon_prev01_month_active.png")}
									}
									&-week{width: 18px;height: 33px;background: url("/assets/images/with_parents_progress/icon_prev01_week.png") repeat-x 0 0;
										&.active{background-image: url("/assets/images/with_parents_progress/icon_prev01_week_active.png")}
									}
								}
								&-next{
									&-month{width: 30px;height: 33px;background: url("/assets/images/with_parents_progress/icon_next01_month.png") repeat-x 0 0;
										&.active{background-image: url("/assets/images/with_parents_progress/icon_next01_month_active.png")}
									}
									&-week{width: 18px;height: 33px;background: url("/assets/images/with_parents_progress/icon_next01_week.png") repeat-x 0 0;
										&.active{background-image: url("/assets/images/with_parents_progress/icon_next01_week_active.png")}
									}
								}
							}
						}
					}
					&-scrollbar{
						top: 0 !important;
						right: 8px !important;
						height: 578px !important;
						width: 3px !important;
						background-color: transparent !important;

						&-drag{background-color: #d5d5d5;}
					}
				}
			}
			.btn{display: block;width: 100%;height: 100%;}
		}
	//}
